import React, { useContext } from 'react'
import { AppContext } from '../App';
import { Link } from 'react-router-dom';
import SingleProduct from '../pages/SingleProduct';

const HomeCard = (props) => {

  const [cart,setCart]=useContext(AppContext);
  function AddToCart(product,e)
  {
    e.preventDefault();
      const _cart={...cart};

      const id=product.id;
      if(_cart.items[id])
      {
          _cart.items[id]++;
      }
      else
      {
          _cart.items[id]=1;
      }
      _cart.itemsCount++;
    
      _cart.totalPrice+=product.price;
     console.log(_cart.totalPrice);

     setCart(_cart);
  }
  function BuyNow(e)
  {
    e.preventDefault();
  }
  return (
    
    <div className=''>
        <Link to={`/SingleProduct/${props.product.id}`} product={props.product}>
        <div className="bg-white flex flex-col w-full  rounded-lg shadow-lg shadow-violet-300 overflow-hidden md:m-4 p-3 border-b-2 border-r-2 border-gray-200 ">
                <img src={props.product.image} className="w-[100%] max-h-[100px]  min-h-[100px] md:max-h-[200px] md:min-h-[200px] object-contain"/>
                <div className="relative md:h-[200px] h-[150px] md:p-3 p-1 overflow-hidden flex flex-col items-center md:gap-3 gap-1">
                    <h3 className="text-violet-900 md:text-xl text-md overflow-text-2 text-ellipsis text-center ">{props.product.title}</h3>
                    <span className="md:text-xl text-sm rounded pl-2 bg-violet-500  flex justify-center items-center align-center content-center">{props.product.rating.rate} ⭐</span>
                    <span className="text-green-700 font-bold md:text-lg text-md">Rs. {props.product.price} Only</span>
                    <div className='w-full md:min-h-[50px] min-h-[30px]  grid grid-cols-2 absolute bottom-0 md:gap-3 gap-1'>
                        <button onClick={(e)=>{AddToCart(props.product,e)}} className=" leading-4 text-sm h-full shadow-xl rounded-lg bg-violet-700 text-white active:bg-violet-900 hover:cursor-pointer md:text-xl active:shadow-none active:text-gray-300 p-1">Add To Cart</button>
                        <button onClick={(e)=>{BuyNow(e)}} className="text-sm h-full shadow-xl rounded-lg bg-green-600 text-white active:bg-green-700 hover:cursor-pointer active:shadow-none active:text-gray-300 p-1 md:text-xl">Buy Now</button>
                    </div>
                </div>
            </div> 
            </Link>  
    </div>
  )
}

export default HomeCard;