import React, { useState ,useLayoutEffect} from 'react'
import { useParams } from 'react-router-dom';

const SingleProduct = (props) => {
    const params=useParams();
    console.log(params.id);

    const {data}=props;
    const _id=params.id;

    const product=data.filter((p)=>p.id==_id)[0];

    console.log(product)

    useLayoutEffect(() => {
        window.scrollTo(0, 0)
    });

    

  return (
    <div className='w-full overflow-x-hidden  bg-violet-100 min-h-[80vh] pt-4 md:pt-10 pb-3 md:pb-10'>
        <div className='flex flex-col gap-3 items-center container  mx-auto max-w-[95%] md:max-w-[90%]  '>

            <div>
                <h1 className='text-lg md:text-3xl text-center'>{product.title}</h1>
            </div>

            <div className='w-full grid grid-cols-1 md:grid-cols-2 md:mt-5 mt-2 gap-3 mx-auto'>
                <div className='w-[70%] mx-auto p-3 bg-white rounded-xl shadow-lg  flex justify-center items-center'>
                        <img src={product.image} className='h-full object-contain' />
                </div>
                <div>
                    <div className='w-full text-xs md:text-xl mt-2'>
                        <p>{product.description}</p>
                    </div>
                    <div className='flex flex-col justify-center gap-3 items-center mt-3 md:mt-5'>
                        <span className="md:text-xl w-fit text-sm rounded pl-2 bg-violet-500  flex justify-center items-center align-center content-center">{product.rating.rate} ⭐</span>
                        <span className="text-violet-700 w-fit font-bold md:text-lg text-md">{product.rating.count} Happy Customers and Counting...</span>
                        <span className="text-green-700 w-fit font-bold md:text-lg text-md">Rs. {product.price} Only</span>
                    </div>
                    <div className='w-full md:min-h-[50px] min-h-[30px]  grid grid-cols-2 mt-4 md:mt-5 md:gap-3 gap-1'>
                        <button  className=" leading-4 text-sm h-full shadow-xl rounded-lg bg-violet-700 text-white active:bg-violet-900 hover:cursor-pointer md:text-xl active:shadow-none active:text-gray-300 p-1">Add To Cart</button>
                        <button className="text-sm h-full shadow-xl rounded-lg bg-green-600 text-white active:bg-green-700 hover:cursor-pointer active:shadow-none active:text-gray-300 p-1 md:text-xl">Buy Now</button>
                    </div>
                </div>
            </div>

        </div>
    </div>
  )
}

export default SingleProduct